 <template>
 	<div class="recommend" ref="recommend">
 		<scroll ref="scroll" class="recommend-content" :data="discList">
 			<div>
	 			<div v-if="recommends.length" class="slider-wraper">
	 				<slider>
	 					<div v-for="item in recommends">
	 						<a :href="item.linkUrl">
	 							<img class="needsclick" @load="imgLoad" :src="item.picUrl"/>
	 						</a>
	 					</div>
	 				</slider>
	 			</div>
	 			<div class="recommend-list">
	 				<h1 class="list-title">热门歌单</h1>
	 				<ul class="item-wrap clearfix">
	 						<li v-for="item in discList" class="item" @click="selectItem(item)">
	 							<div class="icon">
	 								<img v-lazy="item.imgurl" />
	 							</div>
	 							<div class="text">
	 								<h2 class="name" v-html="item.creator.name"></h2>
	 								<p class="desc" v-html="item.dissname"></p>
	 							</div>
	 						</li>
	 				</ul>
	 			</div>
 			</div>
			<div class="load" v-show="!discList.length">
				<loading></loading>
			</div>
 		</scroll>
		 <router-view></router-view>
 	</div>
 </template>
 
<script type="text/ecmascript-6">
	import {getRecommend,getDiscList} from 'api/recommend'/*调入的getRecommend是一个promise函数*/
	import {ERR_OK} from 'api/config'
	import Slider from 'base/slider/slider'
	import Scroll from 'base/scroll/scroll'
	import Loading from 'base/loading/loading'
	import {mapMutations,mapGetters}  from 'vuex'
	import {playlistMixin} from 'common/js/mixin'

	 /*我封装了一个scroll组件来解决移动端的滑屏问题 在scroll中我监听data的数据变化来重新设置屏幕高度
	 *在父组件中我将歌单的数据discLick作为数据传入组件的data，监听发生改变但是
	 *由于js是一个异步加载的过程 我的轮播图组件和我的歌单组件是异步加载 当我的歌单组件先加载完成而轮播图组
	 件后加载完成时就会出现问题 导致我滑屏无法滑动到底部 这时我想到可以监听轮播图的图片加载 当图片加载完成
	 *再刷新一次我的scroll 重新获取高度 但是轮播图有多张这样的话会重复刷新导致资源浪费 但 我们可以在其中加
	 判断 判断一个不存在的数据 然后在创建即可保证只有一次
	 */
	export default {
		mixins:[playlistMixin],
		data(){
			return{
				recommends:[],
				discList:[],
			}
		},
		created(){
			this._getRecommend()
			this._getDiscList()
		},
		methods: {
			handlePlaylist(playlist){
      const bottom = playlist.length > 0 ? '60px':''
      this.$refs.recommend.style.bottom = bottom 
      this.$refs.scroll.refresh()
    },
			selectItem(item){
				this.$router.push({
					path:`/recommend/${item.dissid}`
				})
				this.setDisc(item)
			},
			_getRecommend(){ 
				getRecommend().then((res) =>{
					if(res.code === ERR_OK){
						this.recommends=res.data.slider
					}
				})
			},
			_getDiscList() {
        getDiscList().then((res) => {
          if (res.code === ERR_OK) {
          	
            this.discList = res.data.list
            // console.log(this.discList)
          }
        })
      },
			imgLoad(){
				if(!this.loaderimg){
						this.loaderimg=true
					this.$refs.scroll.refresh()
				
				}
			},
			...mapMutations({
				setDisc:'SET_DISC'
			}),
			...mapGetters([
				'disc',
				'playing'
			])
		},
		components:{
			Slider,
			Scroll,
			Loading
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme 
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
			.load{
				position: absolute
				width:100%
				top:50%
				transform:translateY(-50%)
			}
			.item-wrap
				padding 1%	
				.item
					width 50%
					box-sizing border-box
					display inline-block
					.icon
						padding 2%
						img 
							width 100%
					.text 
						font 14px/25px '宋体'	
						.name
							font-weight bold	
						.desc  
							white-space nowrap
							overflow hidden
							text-overflow ellipsis
</style>


