<template>
	<div class="slider" ref='slider'>
		<div class="slider-group" ref='sliderGroup'>
			<slot></slot>
		</div>
		<div class="dots">
			<span class="dot" v-for="eltm,index in dots" :class="{active:currentPageIndex == index}"></span>
		</div>
	</div>
</template>
<script type="text/ecmascript-6">
	/*首页轮播图的插件
	 *template 确定的一个模版 通过ref进行绑定这样我们在vue中就可以通过this.$refs.sliderGroup来获取这个元素相当于
	 * 原生js中的document的获取方式 slot是一个插槽用来存放我们从外部传入的轮播图数据
	 * div class为dots的部分是用来放置对应的小圆点的 其中的v-for 中的dots的元素的获取是根据divclass=“slider-group”的子集的
	 * 个数来获取的
	 * 
	 * BScroll ：一种移动端的手机滑动的组件
	 * props 中的是外部传入的数据 根据外部组件的调用来确定是否有什么功能
	 * window.addEventListener('resize',function(){对窗口改变这个事件的监听
	 * 	
	 * })
	 * */
	import BScroll from 'better-scroll'
	import {addClass} from 'common/js/dom'
	export default {
		data(){
			return{
				dots: [],
				currentPageIndex:0
			}
		},
		props:{
			loop:{//循环
				type:Boolean,
				default:true
			},
			autoplay:{//自动播放
				type:Boolean,
				default:true
			},
			interval:{
				type:Number,
				default:4000
			}
		},
		mounted(){
			setTimeout(()=>{
				this._setSliderWidth()
				this._initDots()
				this._initSlider()
				
				if(this.autoplay){
				this._play()	
				}
			},20)
			
			window.addEventListener('resize',()=>{
				if(!this.slider){
					console.log(1)
					return
				}
				this._setSliderWidth(true)
				this.slider.refresh()
			})
		},
		methods:{
			_setSliderWidth(isResize){
				this.children = this.$refs.sliderGroup.children
				
				let width = 0
				let sliderWidth = this.$refs.slider.clientWidth
				for (let i=0;i<this.children.length;i++){
					let child = this.children[i]
					addClass(child,'slider-item')
					child.style.width = sliderWidth + 'px'
					width +=sliderWidth
				}
				if(this.loop&&!isResize){
					width +=2*sliderWidth
				}
				this.$refs.sliderGroup.style.width = width + 'px'
			},
			_initDots(){
				this.dots=new Array(this.children.length)
			},
			_initSlider(){
				this.slider = new BScroll(this.$refs.slider,{
					scrollX:true,
					scrollY:false,
					momentum: false,
					snap: true,
					snapLoop:this.loop,
					snapThreshold:0.3,
					snapSpeed:400,
				})
				this.slider.on('scrollEnd',()=>{
					let pageIndex = this.slider.getCurrentPage().pageX
					if(this.loop){
						pageIndex -=1					
					}
					this.currentPageIndex=pageIndex
					if(this.autoplay){
						clearTimeout(this.timer)
						this._play()
					}
				})
			},
			_play(){
					let pageIndex =this.currentPageIndex + 1
					if(this.loop){
						pageIndex +=1
					}
					this.timer = setTimeout(()=>{
						this.slider.goToPage(pageIndex,0,400)
					},this.interval)
			}
		},
		destroyed(){
			clearTimeOut(this.timer)
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    position:relative
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>


