import jsonp from 'common/js/jsonp' /*json返回的是一个promise*/
import {commonParams,options} from './config'/*options回调函数的名字*/
import axios from 'axios'
export function getRecommend(){
	const url='https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'
	
	const data = Object.assign({},commonParams,{/*对传入的对象进行整合成一个新的对象返回*/
		platefrom: 'h5',
		uin: 0,
		needNewCode: 1
	})
	
	return jsonp(url,data,options)
}

export function getDiscList() {
	const url = '/api/getDiscList'
	
	const data = Object.assign({},commonParams,{
		platfrom:'yqq',
		hostUin: 0,
	    sin: 0,
	    ein: 29,
	    sortId: 5,
	    needNewCode: 0,
	    categoryId: 10000000,
	    rnd: Math.random(),
	    format: 'json'
	})
	
	return axios.get(url,{
		params: data
	}).then((res) => {
		return Promise.resolve(res.data)
	})
}

export function getSongList (dissid){
		const Songoptions =  {	param: 'jsonpCallback',
														prefix: 'playlistinfoCallback'
														}
		const url = 'https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg'
	
		const data = Object.assign({},commonParams,{
				type: 1,
				json: 1,
				utf8: 1,
				onlysong: 0,
				disstid: dissid,
				loginUin: 0,
				hostUin: 0,
				notice: 0,
				platform: 'yqq',
				needNewCode: 0
		})
			return jsonp(url,data,Songoptions)
}


